<template>
  <el-container>
    <el-header>
       <header-buttons :showRefresh="true"  @buttonClickEvent="buttonClickEvent" cssType="1"></header-buttons>
    </el-header>
    <header-search
        :show-primary-search="true"
        @refreshTableData="refreshTableData"
        cssType="1"
      >
    </header-search>
    <el-main class="view eq" style='overflow-y:visible !important;'>
      <div class="searchDiv" >
        <span>时间范围</span>
        <el-date-picker
            :clearable="false"
            class="dateTime"
            v-model="dateTime"
            type="datetimerange"
            align="left"
            size="mini"
            unlink-panels
            value-format="yyyy-MM-dd HH:mm:ss"
            :range-separator="BTN_TEXT.TO"
            :start-placeholder="BTN_TEXT.STARTTIME"
            :end-placeholder="BTN_TEXT.ENDTIME"
            :default-time="['00:00:00', '23:59:59']"
            :picker-options='pickerOptions'
          ></el-date-picker>
          <span>班次范围</span>
          <el-select v-model="shiftData"  size="mini" placeholder="请选择">
            <el-option
              v-for="item in shiftOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <el-button type="default" style="margin-left:5px;"  size="mini" @click="reset()" >查询</el-button>
      </div>
      <!-- main table -->
      <el-row class="tableShow" >
        <el-col :span="24">
          <VXETable
            ref="xGrid"
            :tableName="''"
            :height="$options.filters.getTableHeight(230)"
            :tableLoading="mainPage.tableLoading"
            :tableDatas="mainPage.data"
            :tableColumns="mainPage.columns"
            :highlightCurrentRow="true"
            :tablePage="mainPage.page"
            @sortChange="sortChange"
            @onTablePageChange="handleMainPageChange"
          >
          </VXETable>
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>

<script>
/*
 * @Author: zhxy
 * @Date: 2021-08-12 09:05:28
 * @LastEditors: ZJM
 * @LastEditTime: 2022-05-22 14:28:53
 * @Description: file content
 */
// import { CommonService } from '@/services/common.service';
// 引入通用接口
import { searchCondetionFunc } from '@/utils/publicFunction';
// 引入table组件
import VXETable from '@/components/common/Table';
import {
  EQUIPMENT_DATA,
  BTN_TEXT,
  PAGE_TITLE,
  ENERGY,
  MAINTAINTASK_STATUS,
  EQUIPMENT_MANAGEMENT_REPORT,
  // EQUIPMENT_STATUS_REPORT,
  STATUS_CODE,
  TITLE,
  MSG,
  VARIABLE
} from '@/utils/constantsFile';
// message service
import { showWarningMessage } from '@/utils/message';
import { deviceIdleGetIdleProductReport, configurationGetConfigurationByEnum } from '@/api/statusEquipmentReport.js';
// 提示信息
import { showErrorNotify } from '@/utils/notify';

export default {
  name: 'EquipmentStatus',
  components: {
    VXETable
  },
  inject: ['reload'],
  data() {
    return {
      VARIABLE: VARIABLE,
      MAINTAINTASK_STATUS: MAINTAINTASK_STATUS,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      BTN_TEXT: BTN_TEXT,
      PAGE_TITLE: PAGE_TITLE,
      ENERGY: ENERGY,
      TITLE: TITLE,
      MSG: MSG,
      maxHeight: document.body.clientHeight - 250,
      activeTab: 0,
      titleName: '',
      mainPage: {
        show: true,
        page: {
          total: 0,
          currentPage: 1,
          pageSize: 10,
          align: 'right',
          pageSizes: [10, 20, 50, 100, 200, 500],
          layouts: [
            'Sizes',
            'PrevPage',
            'Number',
            'NextPage',
            'Total'
          ],
          perfect: true
        },
        data: [
        ],
        columns: [
          {
            field: 'groupName',
            title: EQUIPMENT_MANAGEMENT_REPORT.WORKSHOP_NAME,
            width: 'auto',
            align: 'center'
            // fixed: 'left'
          },

          {
            field: 'deviceName',
            title: EQUIPMENT_MANAGEMENT_REPORT.DEVICE_NAME,
            width: 'auto',
            align: 'center',
            sortable: true
            // fixed: 'left'
          },
          {
            field: 'devicePriCode',
            title: '设备编号',
            width: 'auto',
            align: 'center',
            sortable: true
            // fixed: 'left'
          },
          {
            field: 'typeName',
            title: EQUIPMENT_MANAGEMENT_REPORT.DEVICE_TYPE,
            width: 'auto',
            align: 'center',
            sortable: true
            // fixed: 'left'
          },
          {
            field: 'idleTimeSum',
            title: '总空闲时间(分钟)',
            width: 'auto',
            align: 'center'
            // formatter: this.propertyFormat
            // headerClassName: 'run'
            // fixed: 'left'
          },
          {
            field: 'idleProductAvgTime',
            title: '平均生产单件空闲时间(秒)',
            width: 'auto',
            align: 'center'
            // headerClassName: 'run',
            // formatter: this.hourFormat
            // fixed: 'left'
          },
          {
            field: 'productionValue',
            title: '时间段' + this.$store.state.pageInfo.pageConfigInfo.yieldKey,
            width: 'auto',
            align: 'center'
            // headerClassName: 'run',
            // formatter: this.hourFormat
            // fixed: 'left'
          }
        ],
        tableLoading: false

      },
      isSearchBoxShow: false,
      orderByData: {},
      searchList: [],
      // 指标区间查询参数
      specialSearchList: [],
      searchData: {
        conditionJson1: []
      },
      pickerOptions: {
        disabledDate: (current) => {
          return current.getTime() > this.moment(`${this.moment(Date.now()).format('YYYY-MM-DD')} 23:59:59`).format('x');
        }
      },
      dateTime: [],
      dateTime2: [new Date(2016, 9, 10, 0, 0), new Date(2016, 9, 10, 23, 59)],
      // 保存搜索内输入的搜索条件，防止翻页时变回无条件查询
      savedSearchCondition: [],
      // 班次列表
      shiftOptions: [],
      // 班次值
      shiftData: null
    };
  },
  watch: {
    activeTab: function() {
      this.getQueryParameter();
      this.searchData = {
        conditionJson1: []
      };
      this.findList();
    }
  },
  methods: {
    // 按钮触发事件
    buttonClickEvent(type) {
      this[`${type}`]();
    },
    showSearchCard() {
      this.isSearchBoxShow = !this.isSearchBoxShow;
    },
    reset() {
      this.mainPage.currentPage = 1;
      this.mainPage.pageSize = 10;
      this.findList();
    },
    handleMainPageChange(currentPage, pageSize) {
      this.mainPage.page.currentPage = currentPage;
      this.mainPage.page.pageSize = pageSize;
      this.findList();
    },
    refresh() {
      this.findList();
    },
    async findList() {
      // if(this.dateTime)

      try {
        this.mainPage.tableLoading = true;

        var conditionJson = {
          conditionJson1: [],
          operatorGroup: 1,
          conditionJson2: [],
          size: this.mainPage.page.pageSize,
          current: this.mainPage.page.currentPage
        };
        if (this.savedSearchCondition.length > 0) {
          conditionJson = searchCondetionFunc(...this.savedSearchCondition);
        }

        var param = {
          startTime: '',
          endTime: '',
          shiftStart: '',
          shiftEnd: ''
        };

        if (this.dateTime) {
          param.startTime = this.dateTime[0];
          param.endTime = this.dateTime[1];
        }
        console.log(this.shiftData, this.shiftOptions, this.shiftOptions[this.shiftData]);
        if (this.shiftData !== null) {
          // eslint-disable-next-line no-unused-expressions
          param.shiftStart = this.shiftOptions[this.shiftData].shiftStart;
          param.shiftEnd = this.shiftOptions[this.shiftData].shiftEnd;
        }

        console.log(this.dateTime, this.dateTime2);

        const data = await deviceIdleGetIdleProductReport(param, conditionJson);

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.mainPage.data = data.rows;
          this.mainPage.page.total = data.total;
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.mainPage.tableLoading = false;
      }
    },

    sortChange(property, order) {
      this.orderByData = {};
      this.orderByData[property] = order;
      this.handleMainPageChange(1, this.mainPage.page.pageSize);
    },
    propertyFormat({ cellValue }) {
      return cellValue + '%';
    },
    hourFormat({ cellValue }) {
      return (cellValue / 60).toFixed(2);
    },
    async exportTable() {
      try {
        this.mainPage.tableLoading = true;

        var conditionJson = {
          conditionJson1: [],
          operatorGroup: 1,
          conditionJson2: [],
          size: 99999999,
          current: this.mainPage.page.currentPage
        };
        if (this.savedSearchCondition.length > 0) {
          conditionJson = searchCondetionFunc(...this.savedSearchCondition);
        }

        var param = {
          startTime: '',
          endTime: '',
          shiftStart: '',
          shiftEnd: ''
        };

        if (this.dateTime) {
          param.startTime = this.dateTime[0];
          param.endTime = this.dateTime[1];
        }
        console.log(this.shiftData, this.shiftOptions, this.shiftOptions[this.shiftData]);
        if (this.shiftData !== null) {
          // eslint-disable-next-line no-unused-expressions
          param.shiftStart = this.shiftOptions[this.shiftData].shiftStart;
          param.shiftEnd = this.shiftOptions[this.shiftData].shiftEnd;
        }

        console.log(this.dateTime, this.dateTime2);

        const data = await deviceIdleGetIdleProductReport(param, conditionJson);

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          var arr = [];
          arr = data.rows.map((row) => ({
            ...row,
            ...row.deviceLiveReportVo
          }));
          this.$refs.xGrid.exportData('设备空闲报表', arr);
          // this.mainPage.page.total = data.total;
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.mainPage.tableLoading = false;
      }
    },
    /**
     * @description: 刷新表格数据
     * @param 无
     * @return void
     * @author: cyb
     */
    refreshTableData(savedSearchCondition) {
      if (savedSearchCondition) {
        this.savedSearchCondition = [...savedSearchCondition];
        this.mainPage.page.currentPage = 1;
      }
      this.findList();
    },
    async getSystemShift() {
      try {
        const data = await configurationGetConfigurationByEnum({
          configurationTypeEnum: 'SHIFT_TIME'
        }, {});

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          console.log(data);
          if (data.rows.processConfiguration) {
            var jsonArr = JSON.parse(data.rows.processConfiguration);
            this.shiftOptions = [];
            this.shiftData = null;
            var index = 0;
            for (var key in jsonArr) {
              var json = {
                value: index,
                label: key + ' ' + jsonArr[key],
                shiftStart: jsonArr[key].split('-')[0],
                shiftEnd: jsonArr[key].split('-')[1]
              };
              ++index;
              this.shiftOptions.push(json);
            }
          }
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    }
  },
  created() {
    this.getSystemShift();
    this.dateTime = [
    `${this.moment(new Date()).format('YYYY-MM-DD')} 00:00:00`,
    `${this.moment(new Date()).format('YYYY-MM-DD')} 23:59:59`
    ];
    // this.getQueryParameter();
    this.findList();
  }
};

</script>
<style lang="less" scoped>
.view {
    line-height: normal;
    background-color: white;
    overflow: auto !important;
  }
  .tableShow{
    margin: 0 10px;
  }
  .el-container{
    height: calc(100vh - 90px) !important;
    /deep/ .run{
      color:#027df9;
    }
    /deep/ .free{
      color:#fecf11;
    }
    /deep/ .offline{
      color:#5a6167;
    }
    /deep/ .warn{
      color:#c74746
    }

  }
  .el-header {
    height: 41px !important;
    line-height: 40px;
    width: 100%;
    border-bottom: 1px solid #ddd;
}

  #tabs {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
     margin: 10px 0px 10px 0px;
    overflow: hidden;
  }
  #tabs ul {
    line-height: 37px;
    // width: 249px;
    float: left;
    margin: 0;
    padding-left: 0;
    // display: inline-block;
    // border-bottom: 1px solid black;
  }
  #tabs ul li {
    width: 60px;
    height: 35px;
    line-height: 35px;
    padding: 0px 10px;
    background-color: rgb(255, 255, 255);;
    color: black;
    display: inline-flex;
    border: 1px solid #ddd;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    font-size: 14px;
    // margin-bottom: 15px;
  }
  #tabs ul li.active {
    background: rgb(27, 137, 247);
    color: #fff;
  }

  .run{
    color:#027df9;
  }
  .free{
    color:#fecf11;
  }
  .offline{
    color:#5a6167;
  }
  .warn{
    color:#c74746
  }
.search-fixed{
  text-align: center;
  display: grid;
  grid-template-columns: 1fr 80px 1fr;
  grid-template-rows: repeat(1, 44px);
  column-gap: 20px;
  &>div>span{
      display: inline-block;
      min-width: 80px;
      margin-right:20px;
  }
  .width-special{
      display: inline-block;
      text-align: left;
  }
}
// 以下宽度用于让日期范围选择同search-box中的下拉框对齐
@media screen and (min-width: 1440px) {
  .width-special {
    width: 370px;
  }

}
@media screen and (min-width: 1680px) and (max-width: 1920px) {
  .width-special {
      width: 430px;
    }
}
@media screen and (min-width: 1920px) {
  .width-special {
      width: 510px;
    }
}
.searchDiv{
  text-align: left;
  padding: 5px;
  span{
    margin: 5px;
  }
}
</style>
